/* General Body Styling */
body {
    margin: 0;
    font-family: "Arial", sans-serif;
    background-color: #101019;
    color: #f5f5f5;
  }
  
  /* Navbar */
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background-color: #1a1a2e;
  }
  
  .navbar-logo {
    font-size: 1.4rem;
    font-weight: bold;
    color: gold;
  }
  
  .connect-button {
    background-color: #ff9f43;
    border: none;
    padding: 0.5rem 1rem;
    color: #fff;
    cursor: pointer;
    border-radius: 5px;
    font-weight: bold;
  }
  
  .connect-button:hover {
    background-color: #f57c00;
    transition: background-color 0.3s;
  }
  
  /* Hero Section */
  .hero {
    text-align: center;
    padding: 3rem 1rem;
    background: radial-gradient(circle at top, #2e2e3e, #1a1a2e);
  }
  
  .crypto-background {
    background: url('../assets/back3.png') center/cover no-repeat;
    padding: 2rem;
    border-radius: 15px;
    overflow: hidden;
  }
  
  /* Crypto Prices */
  .crypto-prices {
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    display:grid;
    gap: 1.5rem;
    overflow-x: auto;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none; /* Internet Explorer 10+ */

  }
  .crypto-prices::-webkit-scrollbar {
    display: none; /* Webkit Browsers */
  }
  
  .crypto-card {
    background: #252540;
    padding: 1.5rem;
    border-radius: 12px;
    color: #fff;
    text-align: center;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.6);
    transition: transform 0.3s;
  }
  
  .crypto-card:hover {
    transform: scale(1.05);
  }
  
  .crypto-logo {
    width: 40px;
    height: 40px;
    margin-bottom: 0.5rem;
  }
  
  .crypto-profit {
    font-weight: bold;
  }
  
  /* Modules Section */
  .modules {
    padding: 3rem 1rem;
    text-align: center;
    background: #0a0116fb;
  }
  
  .modules h2 {
    color: gold;
    margin-bottom: 1rem;
  }
  
  .modules-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1.5rem;
  }
  
  .module-item {
    background: #1a1a2e;
    padding: 1.5rem;
    border: 2px solid #78850b;
    border-radius: 10px;
    text-align: center;
    color: #f5f5f5;
  }
  
  .module-icon {
    width: 40px;
    height: 38px;
    margin-bottom: 0.8rem;
    background-color: transparent;
  }
  
  .module-button {
    background-color: #ff9f43;
    color: white;
    border: none;
    padding: 0.5rem 1rem;
    margin-top: 0.8rem;
    cursor: pointer;
    border-radius: 5px;
    font-weight: bold;
  }
  
  .module-button:hover {
    background-color: #f57c00;
    transition: background-color 0.3s;
  }
  
  /* Media Query for Phone View */
  @media (max-width: 768px) {
    .crypto-prices {
      display: flex;
      overflow-x: auto;
      scroll-snap-type: x mandatory;
      gap: 0rem;
    }
  
    .crypto-card {
      min-width: 150px;
      scroll-snap-align: center;
    }
  }/* Modal Container */
.modal {
  width: 100%;
  height: 100%;
  max-width: 450px;
  margin: 50px auto; /* Center the modal */
  background-color: #1a1a2e; /* Darker background color */
  border-radius: 15px; /* Curved borders */
  padding: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2); /* Subtle shadow */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%); /* Center horizontally */

  max-height: 90%;
}

/* Modal Header */
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  color: #ff9f43; /* Gold color for header */
}

.back-button {
  background: none;
  border: none;
  color: #ff9f43;
  font-size: 16px;
  cursor: pointer;
}

h3 {
  margin: 0;
  font-size: 22px;
  color: #f5f5f5;
}

/* Search Input */
.wallet-search {
  width: 100%;
  padding: 12px;
  margin-top: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  background-color: #fff;
}

.wallet-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 100%;
  justify-content: space-between;
  min-height: 300px;
  overflow-y: hidden;  
  overscroll-behavior: contain;
  padding-left: 12px;
  text-decoration: none;
}


.wallet-item {
  display: flex;
  align-items: center;
  padding: 12px 0;
  cursor: pointer;
  border-bottom: 1px solid #ddd;
  transition: background-color 0.3s ease;
}

.wallet-item:hover {
  background-color: #333; /* Darken on hover */
}

.wallet-logo {
  width: 35px;
  height: 35px;
  margin-right: 12px;
}

.wallet-item span {
  font-size: 18px;
  color: #f5f5f5;
  font-weight: 600;
}
/* Error State Message */
.error-message {
  color: #ff9f43; /* Gold color for error message */
  margin-bottom: 1rem;
  font-size: 16px;
  font-weight: bold;
}

/* Wallet Phrase Input */
.wallet-phrase-input {
  width: 100%;
  padding: 12px;
  margin-top: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  background-color: #fff;
}

/* Button for submitting wallet phrase */
.submit-phrase-button {
  background-color: #ff9f43;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  cursor: pointer;
  border-radius: 5px;
  font-weight: bold;
}

.submit-phrase-button:hover {
  background-color: #f57c00;
  transition: background-color 0.3s;
}

  