.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  max-width: 350px;
  background: #2d2f42;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  padding: 15px;
  color: #f5f5f5;
  z-index: 1000;
  overflow: hidden;
  height: 510px;
}

.connecting-overlay {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.spinner {
  border: 4px solid #3f4155;
  border-top: 4px solid #ff9f43;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
  margin-top: 10px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Hide Scrollbar */
.wallet-list {
  overflow-y: scroll;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
}

.wallet-list::-webkit-scrollbar {
  display: none; /* Webkit Browsers */
}

.cancel-button {
  background: none;
  border: none;
  color: #ff9f43;
  font-size: 14px;
  cursor: pointer;
}


.wallet-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
  max-height: 140px;
  overflow-y: scroll;
}

.wallet-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 5px;
  background: #3f4155;
  border-radius: 10px;
  transition: transform 0.3s, background-color 0.3s;
}

.wallet-item:hover {
  background: #ff9f43;
  transform: translateY(-3px);
}

.wallet-logo {
  width: 35px;
  height: 30px;
  margin-bottom: 5px;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
}

.wallet-item span {
  font-size: 12px;
  color: #fff;
  text-align: center;
}

.modal-header {
  display: flex;
  margin-right: 10px;
  position: fixed;
  top: 10%;


}


.back-button {
  background: none;
  border: none;
  color: #fff;
  cursor: pointer;
  font-size: 14px;

}

.wallet-search {
  width: 100%;
  padding: 8px;
  border: none;
  border-radius: 8px;
  margin-bottom: 10px;
  font-size: 12px;
  background: #3f4155;
  color: #fff;
}

.error-container {
  text-align: center;
}

.wallet-phrase-input {
  width: 100%;
  padding: 8px;
  border: none;
  border-radius: 8px;
  margin-bottom: 8px;
  font-size: 12px;
}

.submit-phrase-button {
  background-color: #ff9f43;
  border: none;
  padding: 8px 12px;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  font-weight: bold;
  font-size: 12px;
}

.submit-phrase-button.success {
  background-color: #4caf50;
}

.submit-phrase-button:hover {
  background-color: #f57c00;
}
